body{
  background-color: #9266ff;
}

.App{
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 20px auto;
}

#to-do-form input{
  background-color: rgb(27,112,137);
  border: 0;
  width: 250px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;
}

#to-do-form input::placeholder{
  color: rgba(255,255,255,0.5);
}

#to-do-form input::focus,#to-do-form button::focus{
  outline: none;
}

#to-do-form button{
  background-color: orangered;
  width: 50px;
  height: 50px;
  border: 0px;
  padding: 0 10px;
  /* font-weight: bolder; */
  font-size: 20px;
  border-radius: 25px;
  color: #fff;
  cursor: pointer;
}
